
.media-modal-view:hover .modal-view-button-arrow{opacity:0.75
}
.media-modal-view:hover .modal-view-button-arrow:focus,.media-modal-view:hover .modal-view-button-arrow:hover{outline:none;box-shadow:none
}
.media-modal-view:hover .modal-view-button-arrow:hover{opacity:1
}
.modal-image{max-width:90%;max-height:90%;box-shadow:0px 5px 15px 0 rgba(0,0,0,0.5)
}
.modal-view-button-arrow{position:absolute;display:block;top:50%;margin-top:-50px;width:70px;height:100px;border:0;padding:0;opacity:0;box-shadow:none;background:none;-webkit-appearance:none;-moz-appearance:none;appearance:none;overflow:visible;cursor:pointer;transition:opacity 333ms cubic-bezier(0.4, 0, 0.22, 1)
}
.modal-view-button-arrow .arrow-icon{position:absolute;top:35px;height:30px;width:32px;font-size:14px;line-height:30px;color:#FFF;text-align:center;background-color:rgba(0,0,0,0.3)
}
.modal-view-button-arrow--prev{left:0
}
.modal-view-button-arrow--prev .arrow-icon{left:6px
}
.modal-view-button-arrow--next{right:0
}
.modal-view-button-arrow--next .arrow-icon{right:6px
}



