
.side-drawer-container{position:fixed;z-index:1000;top:0;left:0;width:100%;height:100%;display:-ms-flexbox;display:flex;-ms-flex-align:stretch;align-items:stretch;transition-duration:0s;transition-property:transform
}
.side-drawer-container-open{transform:translate(0%)
}
.side-drawer-container-closed{transition-delay:0.35s;transform:translate(-100%)
}
.side-drawer-darken{top:0;left:0;width:100vw;height:100vh;position:fixed;z-index:-1;transition:0.35s;transition-property:background-color;background-color:rgba(0,0,0,0.5)
}
.side-drawer-darken-closed{background-color:transparent
}
.side-drawer-click-outside{-ms-flex:1 1 100%;flex:1 1 100%
}
.side-drawer{overflow-x:hidden;transition-timing-function:cubic-bezier(0, 1, 0.5, 1);transition:0.35s;transition-property:transform;margin:0 0 0 -100px;padding:0 0 1em 100px;width:80%;max-width:20em;-ms-flex:0 0 80%;flex:0 0 80%;box-shadow:1px 1px 4px rgba(0,0,0,0.6);box-shadow:var(--panelShadow);background-color:#121a24;background-color:var(--bg, #121a24)
}
.side-drawer-logo-wrapper{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;padding:0.85em
}
.side-drawer-logo-wrapper img{-ms-flex:none;flex:none;height:50px;margin-right:0.85em
}
.side-drawer-logo-wrapper span{overflow:hidden;text-overflow:ellipsis;white-space:nowrap
}
.side-drawer-click-outside-closed{-ms-flex:0 0 0px;flex:0 0 0
}
.side-drawer-closed{transform:translate(-100%)
}
.side-drawer-heading{background:transparent;-ms-flex-direction:column;flex-direction:column;-ms-flex-align:stretch;align-items:stretch;display:-ms-flexbox;display:flex;padding:0;margin:0
}
.side-drawer ul{list-style:none;margin:0;padding:0;border-bottom:1px solid;border-color:#222;border-color:var(--border, #222);margin:0.2em 0
}
.side-drawer ul:last-child{border:0
}
.side-drawer li{padding:0
}
.side-drawer li a{display:block;padding:0.5em 0.85em
}
.side-drawer li a:hover{background-color:#151e2a;background-color:var(--lightBg, #151e2a)
}



