
.mobile-inner-nav{width:100%;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center
}
.mobile-nav-button{display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;width:50px;position:relative;cursor:pointer
}
.alert-dot{border-radius:100%;height:8px;width:8px;position:absolute;left:calc(50% - 4px);top:calc(50% - 4px);margin-left:6px;margin-top:-6px;background-color:red;background-color:var(--badgeNotification, red)
}
.mobile-notifications-drawer{width:100%;height:100vh;overflow-x:hidden;position:fixed;top:0;left:0;box-shadow:1px 1px 4px rgba(0,0,0,0.6);box-shadow:var(--panelShadow);transition-property:transform;transition-duration:0.25s;transform:translateX(0)
}
.mobile-notifications-drawer.closed{transform:translateX(100%)
}
.mobile-notifications-header{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:justify;justify-content:space-between;z-index:1;width:100%;height:50px;line-height:50px;position:absolute;color:var(--topBarText);background-color:#182230;background-color:var(--topBar, #182230);box-shadow:0px 0px 4px rgba(0,0,0,0.6);box-shadow:var(--topBarShadow)
}
.mobile-notifications-header .title{font-size:1.3em;margin-left:0.6em
}
.mobile-notifications{margin-top:50px;width:100vw;height:calc(100vh - 50px);overflow-x:hidden;overflow-y:scroll;color:#b9b9ba;color:var(--text, #b9b9ba);background-color:#121a24;background-color:var(--bg, #121a24)
}
.mobile-notifications .notifications{padding:0;border-radius:0;box-shadow:none
}
.mobile-notifications .notifications .panel{border-radius:0;margin:0;box-shadow:none
}
.mobile-notifications .notifications .panel:after{border-radius:0
}
.mobile-notifications .notifications .panel .panel-heading{border-radius:0;box-shadow:none
}



