
.checkbox{position:relative;display:inline-block;padding-left:1.2em;min-height:1.2em
}
.checkbox-indicator::before{position:absolute;left:0;top:0;display:block;content:'';transition:color 200ms;width:1.1em;height:1.1em;border-radius:2px;border-radius:var(--checkboxRadius, 2px);box-shadow:0px 0px 2px black inset;box-shadow:var(--inputShadow);background-color:#182230;background-color:var(--input, #182230);vertical-align:top;text-align:center;line-height:1.1em;font-size:1.1em;color:transparent;overflow:hidden;box-sizing:border-box
}
.checkbox input[type=checkbox]{display:none
}
.checkbox input[type=checkbox]:checked+.checkbox-indicator::before{color:#b9b9ba;color:var(--text, #b9b9ba)
}
.checkbox input[type=checkbox]:indeterminate+.checkbox-indicator::before{content:'';color:#b9b9ba;color:var(--text, #b9b9ba)
}
.checkbox input[type=checkbox]:disabled+.checkbox-indicator::before{opacity:.5
}
.checkbox>span{margin-left:.5em
}



