
.post-form-modal-view{max-height:100%;display:block
}
.post-form-modal-panel{-ms-flex-negative:0;flex-shrink:0;margin:25% 0 4em 0;width:100%
}
.new-status-button{width:5em;height:5em;border-radius:100%;position:fixed;bottom:1.5em;right:1.5em;background-color:#182230;background-color:var(--btn, #182230);display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;box-shadow:0px 2px 2px rgba(0,0,0,0.3),0px 4px 6px rgba(0,0,0,0.3);z-index:10;transition:0.35s transform;transition-timing-function:cubic-bezier(0, 1, 0.5, 1)
}
.new-status-button.hidden{transform:translateY(150%)
}
.new-status-button i{font-size:1.5em;color:#b9b9ba;color:var(--text, #b9b9ba)
}
@media all and (min-width: 801px){
.new-status-button{display:none
}
}



