
.dark-overlay::before{bottom:0;content:" ";display:block;cursor:default;left:0;position:fixed;right:0;top:0;background:rgba(27,31,35,0.5);z-index:99
}
.dialog-modal.panel{top:0;left:50%;max-height:80vh;max-width:90vw;margin:15vh auto;position:fixed;transform:translateX(-50%);z-index:999;cursor:default;display:block;background-color:#121a24;background-color:var(--bg, #121a24)
}
.dialog-modal.panel .dialog-modal-heading{padding:.5em .5em;margin-right:auto;margin-bottom:0;white-space:nowrap;color:var(--panelText);background-color:#182230;background-color:var(--panel, #182230)
}
.dialog-modal.panel .dialog-modal-heading .title{margin-bottom:0
}
.dialog-modal.panel .dialog-modal-content{margin:0;padding:1rem 1rem;background-color:#151e2a;background-color:var(--lightBg, #151e2a);white-space:normal
}
.dialog-modal.panel .dialog-modal-footer{margin:0;padding:.5em .5em;background-color:#151e2a;background-color:var(--lightBg, #151e2a);border-top:1px solid #121a24;border-top:1px solid var(--bg, #121a24);-ms-flex-pack:end;justify-content:flex-end
}
.dialog-modal.panel .dialog-modal-footer button{width:auto;margin-left:.5rem
}



