
.autosuggest{position:relative
}
.autosuggest-input{display:block;width:100%
}
.autosuggest-results{position:absolute;left:0;top:100%;right:0;max-height:400px;background-color:#151e2a;background-color:var(--lightBg, #151e2a);border-style:solid;border-width:1px;border-color:#222;border-color:var(--border, #222);border-radius:4px;border-radius:var(--inputRadius, 4px);border-top-left-radius:0;border-top-right-radius:0;box-shadow:1px 1px 4px rgba(0,0,0,0.6);box-shadow:var(--panelShadow);overflow-y:auto;z-index:1
}



