
.profile-edit .bio{margin:0
}
.profile-edit input[type=file]{padding:5px;height:auto
}
.profile-edit .banner{max-width:100%
}
.profile-edit .uploading{font-size:1.5em;margin:0.25em
}
.profile-edit .name-changer{width:100%
}
.profile-edit .bg{max-width:100%
}
.profile-edit .current-avatar{display:block;width:150px;height:150px;border-radius:4px;border-radius:var(--avatarRadius, 4px)
}
.profile-edit .oauth-tokens{width:100%
}
.profile-edit .oauth-tokens th{text-align:left
}
.profile-edit .oauth-tokens .actions{text-align:right
}
.profile-edit-usersearch-wrapper{padding:1em
}
.profile-edit-bulk-actions{text-align:right;padding:0 1em;min-height:28px
}
.profile-edit-bulk-actions button{width:10em
}



