
.popper-wrapper{z-index:8
}
.popper-wrapper .popper__arrow{width:0;height:0;border-style:solid;position:absolute;margin:5px
}
.popper-wrapper[x-placement^="top"]{margin-bottom:5px
}
.popper-wrapper[x-placement^="top"] .popper__arrow{border-width:5px 5px 0 5px;border-color:#121a24 transparent transparent transparent;border-color:var(--bg, #121a24) transparent transparent transparent;bottom:-5px;left:calc(50% - 5px);margin-top:0;margin-bottom:0
}
.popper-wrapper[x-placement^="bottom"]{margin-top:5px
}
.popper-wrapper[x-placement^="bottom"] .popper__arrow{border-width:0 5px 5px 5px;border-color:transparent transparent #121a24 transparent;border-color:transparent transparent var(--bg, #121a24) transparent;top:-5px;left:calc(50% - 5px);margin-top:0;margin-bottom:0
}
.popper-wrapper[x-placement^="right"]{margin-left:5px
}
.popper-wrapper[x-placement^="right"] .popper__arrow{border-width:5px 5px 5px 0;border-color:transparent #121a24 transparent transparent;border-color:transparent var(--bg, #121a24) transparent transparent;left:-5px;top:calc(50% - 5px);margin-left:0;margin-right:0
}
.popper-wrapper[x-placement^="left"]{margin-right:5px
}
.popper-wrapper[x-placement^="left"] .popper__arrow{border-width:5px 0 5px 5px;border-color:transparent transparent transparent #121a24;border-color:transparent transparent transparent var(--bg, #121a24);right:-5px;top:calc(50% - 5px);margin-left:0;margin-right:0
}
.dropdown-menu{display:block;padding:.5rem 0;font-size:1rem;text-align:left;list-style:none;max-width:100vw;z-index:10;box-shadow:1px 1px 4px rgba(0,0,0,0.6);box-shadow:var(--panelShadow);border:none;border-radius:4px;border-radius:var(--btnRadius, 4px);background-color:#121a24;background-color:var(--bg, #121a24)
}
.dropdown-menu .dropdown-divider{height:0;margin:.5rem 0;overflow:hidden;border-top:1px solid #222;border-top:1px solid var(--border, #222)
}
.dropdown-menu .dropdown-item{line-height:21px;margin-right:5px;overflow:auto;display:block;padding:.25rem 1.0rem .25rem 1.5rem;clear:both;font-weight:400;text-align:inherit;white-space:normal;border:none;border-radius:0px;background-color:transparent;box-shadow:none;width:100%;height:100%
}
.dropdown-menu .dropdown-item:hover{background-color:#182230;background-color:var(--btn, #182230);box-shadow:none
}
.menu-checkbox{float:right;min-width:22px;max-width:22px;min-height:22px;max-height:22px;line-height:22px;text-align:center;border-radius:0px;background-color:#182230;background-color:var(--input, #182230);box-shadow:0px 0px 2px black inset;box-shadow:var(--inputShadow)
}
.menu-checkbox.menu-checkbox-checked::after{content:''
}



